export default class index extends Component {
  constructor() {
    super();
    this.state = {
      // å°†è·å–åˆ°çš„hashå€¼èµ‹å€¼ç»™isCom
      isCom: window.location.hash.slice(1) || 'ReactBase'
    };

    window.onhashchange = () => {
      // è·å–å¹¶æ›´æ”¹ä¸ºå½“å‰hashå€¼
      this.setState({
        isCom: window.location.hash.slice(1)
      });
    };
  }
  // æ”¶å¬å­ç»„ä»¶ä¼ é€’æ•°æ®
  getSonData = id => {
    window.location.hash = id;
    // è®¾ç½®hashå€¼
  };
  // æ¸²æŸ“å‡½æ•°
  // å¸¦/çš„æ˜¯æ€åšé…çš„å­è·¯ç”±
  render() {
    let Dom = '';
    // å£°æ˜ä¸€ä¸ªdomä½œä¸ºæ¥ä¸‹æ¥æ¡ä»¶æ¸²æŸ“çš„å€¼
    switch (this.state.isCom) {
      // æ ¹æ®hashåšæ¡ä»¶æ¸²æŸ“
      case 'ReactBase':
        Dom = <ReactBase />;
        break;
      case 'ComponentType':
        Dom = <ComponentType />;
        break;
      case 'ReactSenior':
        Dom = <ReactSenior a={this.getSonData} />;
        break;
      case 'third-plugins':
        Dom = <Third />;
        break;
      case 'ReactSenior/Top-Api':
        Dom = <TopApi />;
        break;
      case 'ReactSenior/Top-Jsx':
        Dom = <Jsx />;
        break;
      case 'ReactSenior/High-order-component':
        Dom = <Gaojie />;
        break;
      case 'ReactSenior/life':
        Dom = <Life />;
        break;
      case 'ReactSenior/state':
        Dom = <State />;
        break;
      case 'ReactSenior/chacao':
        Dom = <Chacao />;
        break;
      default:
    }
    return <Mylayout getsondata={this.getSonData}>{Dom}</Mylayout>;
  }
}
